/* visual */
.visual { margin-top: 120px; background-image: url(../images/visual_bg.jpg); background-position: center; }
.visual .inner { height: 646px; }
.visual .title { position: absolute; top: 80px; left: -10px; }
.visual .title .btn { position: absolute; top: 259px; left: 173px; }
.visual .cup1.image { position: absolute; bottom: 0; right: -47px; }
.visual .cup1.text { position: absolute; top: 38px; right: 171px; }
.visual .cup2.image { position: absolute; bottom: 0; right: 162px; }
.visual .cup2.text { position: absolute; top: 321px; right: 416px; }
.visual .spoon { position: absolute; bottom: 0; left: 275px; }

.visual .fade--in { opacity: 0; };

/* notice */
.notice { background-color: transparent; }
.notice .notice-line { position: relative; }
.notice .notice-line .bg-left { position: absolute; top: 0; left: 0; width: 50%; height: 100%; background-color: #333; }
.notice .notice-line .bg-right { position: absolute; top: 0; right: 0; width: 50%; height: 100%; background-color: #f6f5ef; }
.notice .notice-line .inner { height: 62px; display: flex; }
.notice .notice-line .inner .inner__left { width: 60%; height: 100%; background-color: #333; display: flex; align-items: center; }
.notice .notice-line .inner .inner__right { width: 40%; height: 100%; display: flex; justify-content: flex-end; align-items: center; }

.notice .notice-line .inner .inner__left h2 { color: #fff; font-size: 17px; font-weight: 700; margin-right: 20px; }
.notice .notice-line .inner .inner__left .swiper { height: 62px; flex-grow: 1; }
.notice .notice-line .inner .inner__left .notice-line__more { width: 62px; height: 62px; display: flex; justify-content: center; align-items: center; }
.notice .notice-line .inner .inner__left .notice-line__more .material-icons { color: #fff; font-size: 30px; margin-right: 20px; }
.notice .notice-line .inner .inner__right h2 { font-size: 17px; font-weight: 700; margin-right: 20px; }
.notice .notice-line .inner .inner__right .toggle-promotion { width: 62px; height: 62px; cursor: pointer; display: flex; justify-content: center; align-items: center; }
.notice .notice-line .inner .inner__right .toggle-promotion .material-icons { font-size: 30px; }

.notice .notice-line .inner .inner__left .swiper .swiper-slide { height: 62px; display: flex; align-items: center; }
.notice .notice-line .inner .inner__left .swiper .swiper-slide a { color: #fff; }

/* promotion */
.notice .promotion { height: 693px; background-color: #f6f5ef; position: relative; transition: height .4s; overflow: hidden; }
.notice .promotion.hide { height: 0; }

.notice .promotion .swiper { width: calc(819px * 3 + 20px); height: 553px; position: absolute; top: 40px; left: 50%; margin-left: calc((819px * 3 + 20px) / -2); }
.notice .promotion .swiper .swiper-slide { opacity: .5; transition: opacity 1s; }
.notice .promotion .swiper .swiper-slide-active { opacity: 1; }
.notice .promotion .swiper .swiper-slide .btn { position: absolute; bottom: 0; left: 0; right: 0; margin: auto; }
.notice .promotion .swiper-pagination { bottom: 40px; left: 0; right: 0; margin: auto;}
.notice .promotion .swiper-pagination .swiper-pagination-bullet { background-image: url(../images/promotion_slide_pager.png); width: 12px; height: 12px; margin-right: 6px; outline: 0; background-color: transparent; }
.notice .promotion .swiper-pagination .swiper-pagination-bullet:last-child { margin-right: 0; }
.notice .promotion .swiper-pagination .swiper-pagination-bullet-active { background-image: url(../images/promotion_slide_pager_on.png); }

.notice .promotion .swiper-button-prev, .notice .promotion .swiper-button-next { width: 42px; height: 42px; border: 2px solid #333; border-radius: 50%; position: absolute; top: 300px; z-index: 1; cursor: pointer; outline: none; display: flex; justify-content: center; align-items: center; background-color: rgba(255,255,255,.3); transition: .4s; }

.notice .promotion .swiper-button-prev { left: 50%; margin-left: -480px; color: #669900; }
.notice .promotion .swiper-button-next { right: 50%; margin-right: -480px; color: #669900; }
.notice .promotion .swiper-button-prev:after { content: ""; }
.notice .promotion .swiper-button-next:after { content: ""; }
.notice .promotion .swiper-button-prev:hover { background-color: #333; color: #fff; }
.notice .promotion .swiper-button-next:hover { background-color: #333; color: #fff; }

/* rewards */
.rewards { position: relative; }
.rewards .bg-left, .rewards .bg-right { width: 50%; height: 100%; position: absolute; top: 0; }
.rewards .bg-left { background-color: #272727; left: 0; }
.rewards .bg-right { background-color: #d5c798; right: 0; }
.rewards .inner { background-image: url(../images/rewards.jpg); height: 241px; }

.rewards .btn-group { width: 250px; display: flex; flex-wrap: wrap; position: absolute; bottom: 24px; right: 0; }
.rewards .btn-group .btn.sign-up { margin-right: 10px;}
.rewards .btn-group .btn.sign-in { width: 110px; }
.rewards .btn-group .btn.gift { margin-top: 10px; flex-grow: 1; }

/* youtube video */
.youtube { position: relative; height: 700px; background-color: #333; overflow: hidden; }
.youtube .youtube__area { width: 1920px; background-color: #ffa500; position: absolute; left: 50%; margin-left: calc(1920px / -2); top: 50%; margin-top: calc(1920px * 9 / 16 / -2); }
.youtube .youtube__area::before { content: ""; display: block; width: 100%; height: 0; padding-top: 56.25%; }
.youtube .youtube__cover { background-image: url(../images/video_cover_pattern.png); background-color: rgba(0,0,0,.3); position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

#player { width: 100%; height: 100%; position: absolute; top: 0; left: 0; }

.youtube .inner { height: 700px; }
.youtube .inner .floating1 { position: absolute; top: 50px; left: 0; } 
.youtube .inner .floating2 { position: absolute; top: 350px; left: 150px; } 

/* season product */
.season-product { background-image: url(../images/season_product_bg.jpg); }
.season-product .inner { height: 400px; }
.season-product .floating3 { position: absolute; top: -200px; right: 0; } 
.season-product .text-group { position: absolute; top: 110px; right: 100px; }
.season-product .text-group .title { margin-bottom: 10px; }
.season-product .text-group .description { margin-bottom: 15px; }

/* reserve coffee */
.reserve-coffee { background-image: url(../images/reserve_bg.jpg); }
.reserve-coffee .inner { height: 400px; }
.reserve-coffee .reserve-logo { position: absolute; top: 110px; left: 0; }
.reserve-coffee .text-group { position: absolute; top: 124px; left: 200px; }
.reserve-coffee .product { position: absolute; top: 0; right: 0; }

/* pick your favorite */
.pick-your-favorite { background-image: url(../images/favorite_bg.jpg); background-repeat: no-repeat; background-position: center; background-attachment: fixed; background-size: cover; }
.pick-your-favorite .inner { padding: 110px 0; }
.pick-your-favorite .text-group { margin-left: 100px; display: flex; width: 362px; flex-wrap: wrap; justify-content: flex-end; }
.pick-your-favorite .text-group .title { margin-bottom: 40px; }
.pick-your-favorite .text-group .description { margin-bottom: 40px; }

/* reserve store */
.reserve-store { background-image: url(../images/reserve_store_bg.jpg); background-repeat: no-repeat; background-position: center; background-attachment: fixed; background-size: cover; }
.reserve-store .inner { height: 600px; display: flex; justify-content: center; align-items: center; }

.reserve-store .medal { width: 334px; height: 334px; perspective: 600px; }
.reserve-store .medal .front, .reserve-store .medal .back { width: 334px; height: 334px; backface-visibility: hidden; transition: 1s; position: absolute; }

.reserve-store .medal .front { transform: rotateY(0deg); }
.reserve-store .medal:hover .front { transform: rotateY(-180deg); }
.reserve-store .medal .back { transform: rotateY(-180deg); }
.reserve-store .medal:hover .back { transform: rotateY(0deg); }

.reserve-store .medal .back .btn { position: absolute; top: 240px; left: 0; right: 0; margin: auto; }

/* find the store */
.find-the-store { background-image: url(../images/find_store_bg.jpg); }
.find-the-store .inner { height: 400px; }
.find-the-store .texture1 { position: absolute; left: 400px; }
.find-the-store .texture2 { position: absolute; bottom: 0; right: 0; }
.find-the-store .picture { border-radius: 50%; box-shadow: 2px 2px 8px rgba(0,0,0,.5); position: absolute; }
.find-the-store .picture.picture1 { top: -60px; left: 0; }
.find-the-store .picture.picture2 { top: 150px; left: 250px; }
.find-the-store .text-group { position: absolute; top: 120px; left: 550px; }
.find-the-store .text-group .title { margin-bottom: 20px; }
.find-the-store .text-group .description { margin-bottom: 20px; }

/* awards */
.awards { background-color: #272727; position: relative; }
.awards .inner { padding: 40px 0; }
.awards .swiper { width: 100%; height: 40px; }

.awards .swiper-button-prev, .awards .swiper-button-next { width: 42px; height: 42px; border: 2px solid rgba(255,255,255,.5); border-radius: 50%; position: absolute; z-index: 1; cursor: pointer; outline: none; display: flex; justify-content: center; align-items: center; background-color: rgba(255,255,255,.3); transition: .4s; }

.awards .swiper-button-prev { left: 50%; margin-left: -480px; color: #fff; }
.awards .swiper-button-next { right: 50%; margin-right: -480px; color: #fff; }
.awards .swiper-button-prev:hover { background-color: #333; }
.awards .swiper-button-next:hover { background-color: #333; }
.awards .swiper-button-prev:after, .awards .swiper-button-next:after { content: ""; }

/* top scroll button */
#to-top { width: 42px; height: 42px; background-color: #333; color: #fff; border: 2px solid #fff; border-radius: 10px; cursor: pointer; display: flex; justify-content: center; align-items: center; position: fixed; bottom: 30px; right: 30px; z-index: 9; opacity: 0; }